<template>
  <v-container fluid>
    <v-row>
      <v-col lg="2" sm="3" cols="12" class="ml-4 d-flex align-self-center">
        <v-checkbox
          class="ma-0"
          v-model="isDateSetting"
          :label="$t('facility.startDateSetting')"
          :hide-details="true"
        ></v-checkbox>
      </v-col>
      <v-col lg="3" sm="4" cols="12">
        <v-menu
          v-model="menu"
          :close-on-content-click="false"
          :nudge-right="40"
          transition="scale-transition"
          offset-y
          min-width="auto"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              v-model="startDate"
              readonly
              v-bind="attrs"
              v-on="on"
              dense
              outlined
              :disabled="!isDateSetting"
              :hide-details="true"
            ></v-text-field>
          </template>
          <v-date-picker
            v-model="startDate"
            @input="menu = false"
            locale="ja-jp"
          ></v-date-picker>
        </v-menu>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12" md="6">
        <v-simple-table class="custom-table">
          <template v-slot:default>
            <thead>
            <tr>
              <th class="text-left">
                {{  $t('facility.accomodationFee') }}
              </th>
              <th class="text-left">
                {{  $t('facility.lodgingTax') }}
              </th>
              <th></th>
            </tr>
            </thead>
            <tbody>
            <tr
              v-for="item in data.parkingTaxes"
              :key="item.id"
            >
              <td>{{ $t('facility.nOrMore', { value: item.accomodationFee | currency }) }} </td>
              <td class="font-weight-medium">{{ item.lodgingTax | currency }}</td>
              <td class="text-center">
                <v-btn
                  color="primary"
                  icon
                >
                  <v-icon>
                    mdi-pencil
                  </v-icon>
                </v-btn>
                <v-btn
                  color="primary"
                  icon
                >
                  <v-icon>
                    mdi-trash-can-outline
                  </v-icon>
                </v-btn>
              </td>
            </tr>
            </tbody>
          </template>
        </v-simple-table>
        <v-container fluid class="clearfix">
          <v-btn
            color="primary"
            outlined
            class="float-right"
          >
            {{  $t('buttons.add') }}
          </v-btn>
        </v-container>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
// import FormRow from '@/components/Form/FormRow'
// import FormDivider from '@/components/Form/FormDivider'

export default {
  components: {
  },
  props: ['data'],
  data () {
    return {
      isDateSetting: false,
      menu: false,
      startDate: new Date(2021, 3, 26).toISOString().substr(0, 10)
    }
  }
}
</script>

