<template>
  <v-dialog
    v-model="isModalOpen"
    persistent
    max-width="500px"
  >
    <template v-slot:activator="{ on, attrs }">
      <v-btn
        color="primary"
        dark
        type="submit"
        class="float-right"
        v-bind="attrs"
        v-on="on"
      >
        <v-icon left>
          mdi-plus
        </v-icon>
        <slot />
      </v-btn>
    </template>
    <v-card class="pa-4">
      <v-form
        @submit.prevent="onSubmit"
        class="custom-form"
        ref="form"
      >
        <v-card-title>
          <span class="headline grey--text">{{ title }}</span>
        </v-card-title>
        <v-container>
          <form-row :larger-key="true">
            <template v-slot:key>
              <label class="custom-form-label" :for="'loan-items'">
                {{ $t('facility.itemsForLoan') }}
              </label>
            </template>
            <template v-slot:value>
              <v-select
                id="loan-items"
                v-model="loanItem"
                :items="loanItems"
                :rules="rules.loanItems"
                dense
                outlined
              ></v-select>
            </template>
          </form-row>
          <form-row :larger-key="true">
            <template v-slot:key>
              <label
                class="custom-form-label"
                :for="'total-quantity'">
                {{ $t('facility.totalQuantity') }}
              </label>
            </template>
            <template v-slot:value>
              <v-text-field
                id="total-quantity"
                type="number"
                v-model="totalQuantity"
                :rules="rules.totalQuantity"
                dense
                outlined
              ></v-text-field>
            </template>
          </form-row>
          <form-row :larger-key="true">
            <template v-slot:key>
              <label
                class="custom-form-label"
              >
                {{ $t('facility.loanedPriceExcluding') }}
              </label>
            </template>
            <template v-slot:value>
              <div class="float-right">
                {{ 0 | currency }}
              </div>
            </template>
          </form-row>
          <form-row :larger-key="true">
            <template v-slot:key>
              <label
                class="custom-form-label"
              >
                {{ $t('facility.paymentUnit') }}
              </label>
            </template>
            <template v-slot:value>
              <div class="float-right">
                {{ $t('facility.nNight', { value: 1 }) }}
              </div>
            </template>
          </form-row>
        </v-container>
        <v-card-actions class="mt-2">
          <v-spacer></v-spacer>
          <v-btn
            color="errorButton"
            dark
            @click="isModalOpen = false"
          >
            {{ $t('facility.cancel') }}
          </v-btn>
          <v-btn
            type="submit"
            color="primary"
            dark
          >
            {{ $t('buttons.retainAndAdd') }}
          </v-btn>
        </v-card-actions>
      </v-form>
    </v-card>
  </v-dialog>
</template>

<script>
import FormRow from '@/components/Form/FormRow'
export default {
  name: 'AddLoanProductsModal',
  components: { FormRow },
  props: ['data'],
  data () {
    return {
      title: this.$t('facility.registeringLoanItems'),
      isModalOpen: false,
      loanItem: 1,
      totalQuantity: 1,
      loanItems: this.$t('facility.data.loanItems').map((x, i) => ({
        text: x,
        value: i + 1
      })),
      rules: {
        loanItems: [
          v => !!v || this.$t('rules.loanItemsRequired')
        ],
        totalQuantity: [
          v => !!v || this.$t('rules.totalQuantityRequired'),
          v => (v && v > 0) || this.$t('rules.totalQuantityMoreThanN')
        ]
      }
    }
  },
  methods: {
    async onSubmit () {
      if (!this.$refs.form.validate()) {
        return
      }

      this.data.loanProducts.push({
        title: this.loanItems.find(x => x.value === this.loanItem).text,
        quantity: this.totalQuantity,
        price: 0,
        paymentUnit: ''
      })

      this.isModalOpen = false
    }
  }
}
</script>

