<template>
  <v-main>
    <v-container fluid  class="pa-0 ma-0 clearfix">
      <add-loan-products-modal :data="data">
        {{ $t('buttons.registerLoanItems') }}
      </add-loan-products-modal>
    </v-container>
    <v-container fluid>
      <v-simple-table class="custom-table">
        <template v-slot:default>
          <thead>
          <tr>
            <th class="text-left">
              {{ $t('facility.itemsForLoan') }}
            </th>
            <th class="text-left">
              {{ $t('facility.totalQuantity') }}
            </th>
            <th class="text-left">
              {{ $t('facility.loanedPriceIncluding') }}
            </th>
            <th class="text-left">
              {{ $t('facility.paymentUnit') }}
            </th>
            <th class="text-left"> </th>
          </tr>
          </thead>
          <tbody>
          <tr
            v-for="(loanProduct, i) in data.loanProducts"
            :key="i"
          >
            <td>{{ loanProduct.title }}</td>
            <td>
              <v-text-field
                id="application-name"
                v-model="loanProduct.quantity"
                dense
                outlined
                :hide-details="true"
                type="number"
                class="width-60-px"
              ></v-text-field>
            </td>
            <td>{{ loanProduct.price | currency }}</td>
            <td>{{ loanProduct.paymentUnit }}</td>
            <td>
              <v-btn
                text
                color="error"
                class="float-right"
                @click="deleteLoanProduct(i)"
              >
                {{ $t('facility.delete')}}
              </v-btn>
            </td>
          </tr>
          </tbody>
        </template>
      </v-simple-table>
    </v-container>
  </v-main>
</template>

<script>
import AddLoanProductsModal from './AddItemModal'
export default {
  components: {
    AddLoanProductsModal
  },
  props: ['data'],
  data () {
    return {
    }
  },
  methods: {
    deleteLoanProduct (index) {
      this.data.loanProducts.splice(index, 1)
    }
  }
}
</script>

