<template>
  <div>
    <h1 class="mx-12 my-4 grey--text font-weight-medium">{{ title }}</h1>
    <template>
      <v-tabs v-model="tab" align-with-title>
        <v-tab v-for="(item, i) in items" :key="i" :href="`#tab-${i}`">{{ item.title }}</v-tab>
      </v-tabs>
    </template>
    <template>
      <v-form
        @submit.prevent="onSubmit"
        class="custom-form form-wrapper"
        ref="form"
      >
        <form-card>
          <v-tabs-items v-model="tab">
            <v-tab-item
              v-for="(item, i) in items"
              :key="i"
              :value="`tab-${i}`"
            >
              <component
                :is="item.component"
                :data="data"
              />
            </v-tab-item>
          </v-tabs-items>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              color="secondary"
              @click="closeNoSave"
            >
              <v-icon left>
                mdi-close
              </v-icon>
              {{ $t('facility.closeWithoutSaving') }}
            </v-btn>
            <v-btn
              color="primary"
              @click="saveUpdate"
            >
              {{ $t('facility.saveAndUpdate') }}
            </v-btn>
            <v-btn
              color="primary"
              type="submit"
            >
              {{ $t('facility.saveAndClose') }}
            </v-btn>
          </v-card-actions>
        </form-card>
      </v-form>
    </template>
  </div>
</template>

<script>
import FacilityInformation from '@/components/Facilities/Settings/Edit/FacilityInformation'
import LoanProducts from '@/components/Facilities/Settings/Edit/LoanProducts/LoanProducts'
import CarPark from '@/components/Facilities/Settings/Edit/CarPark'
import ParkingTax from '@/components/Facilities/Settings/Edit/ParkingTax'
import FormCard from '@/components/Form/FormCard'

export default {
  name: 'EditFacilitySettings',
  data () {
    return {
      title: this.$t('facility.data.facilityEditTitle'),
      tab: null,
      data: {
        id: 1,
        facilityType: this.$t('facility.data.facilityType'),
        facilityName: '',
        showOrder: 1,
        booksUsedMeans: ['', ''],
        address: '',
        tel: ['', '', ''],
        fax: ['', '', ''],
        askCoosleeperAge: false,
        feeCollection: 1,
        parkingTaxes: [
          {
            id: 1,
            accomodationFee: 0,
            lodgingTax: 0
          },
          {
            id: 2,
            accomodationFee: 1,
            lodgingTax: 200
          },
          {
            id: 3,
            accomodationFee: 20000,
            lodgingTax: 500
          }
        ],
        parkingCars: [
          {
            title: this.$t('facility.data.parkingCars')[0],
            carHeight: [156, 199],
            type: 1,
            quantity: 4,
            isWaiting: true
          },
          {
            title: this.$t('facility.data.parkingCars')[1],
            carHeight: [0, 155],
            type: 2,
            quantity: 8,
            isWaiting: true
          }
        ],
        floorPDFPlan: null,
        loanProducts: [
          {
            title: this.$t('facility.data.loanProducts')[0],
            quantity: 3,
            price: 0,
            paymentUnit: ''
          },
          {
            title: this.$t('facility.data.loanProducts')[1],
            quantity: 3,
            price: 0,
            paymentUnit: ''
          },
          {
            title: this.$t('facility.data.loanProducts')[2],
            quantity: 3,
            price: 0,
            paymentUnit: ''
          },
          {
            title: this.$t('facility.data.loanProducts')[3],
            quantity: 0,
            price: 1100,
            paymentUnit: this.$t('facility.nNight', { value: 1 })
          },
          {
            title: this.$t('facility.data.loanProducts')[4],
            quantity: 1,
            price: 2200,
            paymentUnit: this.$t('facility.nNight', { value: 1 })
          },
          {
            title: this.$t('facility.data.loanProducts')[5],
            quantity: 3,
            price: 0,
            paymentUnit: ''
          }
        ]
      },
      items: [
        {
          title: this.$t('routeTitles.facilitySettings'),
          component: FacilityInformation
        },
        {
          title: this.$t('routeTitles.loanProducts'),
          component: LoanProducts
        },
        {
          title: this.$t('routeTitles.carPark'),
          component: CarPark
        },
        {
          title: this.$t('routeTitles.parkingTax'),
          component: ParkingTax
        }
      ]
    }
  },
  components: {
    FacilityInformation,
    FormCard
  },
  methods: {
    onSubmit () {
      if (!this.$refs.form.validate()) {
        return
      }
      // it will be changed when backend is ready
      this.$router.push({ path: '/facility/settings' })
    },

    closeNoSave () {
      // it will be changed when backend is ready
      this.$router.push({ path: '/facility/settings' })
    },
    saveUpdate () {
      if (!this.$refs.form.validate()) {
        return
      }
      console.log('Data is updated')
    }
  }
}
</script>

<style lang="scss">

</style>
