<template>
  <v-container fluid>
    <form-row>
      <template v-slot:key>
        <label class="custom-form-label">
          {{ $t('facility.facilityId') }}
        </label>
      </template>
      <template v-slot:value>
        {{ data.id }}
      </template>
    </form-row>
    <form-row>
      <template v-slot:key>
        <label class="custom-form-label">
          {{  $t('facility.facilityType') }}
        </label>
      </template>
      <template v-slot:value>
        {{ data.facilityType }}
      </template>
    </form-row>
    <form-row>
      <template v-slot:key>
        <label class="custom-form-label" :for="'application-name'">
          {{  $t('facility.applicationName') }}
        </label>
      </template>
      <template v-slot:value>
        <v-row>
          <v-col cols="12" md="5" lg="3">
            <v-text-field
              id="application-name"
              :rules="rules.applicationName"
              dense
              outlined
            ></v-text-field>
          </v-col>
        </v-row>
      </template>
    </form-row>
    <form-row>
      <template v-slot:key>
        <label class="custom-form-label" :for="'screen-display-order'">
          {{  $t('facility.controlScreenDisplayOrder') }}
        </label>
      </template>
      <template v-slot:value>
        <v-row>
          <v-col cols="12" md="5" lg="3">
            <v-text-field
              id="screen-display-order"
              dense
              outlined
              type="number"
            ></v-text-field>
          </v-col>
        </v-row>
      </template>
    </form-row>

    <form-divider />

    <form-row>
      <template v-slot:key>
        <label class="custom-form-label">
          {{  $t('facility.receiptPostalCode') }}
        </label>
      </template>
      <template v-slot:value>
        <v-text-field
          dense
          outlined
          class="width-120-px d-inline-block"
        ></v-text-field>
        <v-icon>
          mdi-minus
        </v-icon>
        <v-text-field
          dense
          outlined
          class="width-120-px d-inline-block"
        ></v-text-field>
      </template>
    </form-row>

    <form-row>
      <template v-slot:key>
        <label class="custom-form-label" :for="'receiptAddress'">
          {{  $t('facility.receiptAddress') }}
        </label>
      </template>
      <template v-slot:value>
        <v-row>
          <v-col cols="12" md="10" lg="7">
            <v-text-field
              id="receiptAddress"
              dense
              outlined
            ></v-text-field>
          </v-col>
        </v-row>
      </template>
    </form-row>

    <form-row>
      <template v-slot:key>
        <label class="custom-form-label">
          {{  $t('facility.receiptTel') }}
        </label>
      </template>
      <template v-slot:value>
        <v-text-field
          dense
          outlined
          class="width-120-px d-inline-block"
        ></v-text-field>
        <v-icon>
          mdi-minus
        </v-icon>
        <v-text-field
          dense
          outlined
          class="width-120-px d-inline-block"
        ></v-text-field>
        <v-icon>
          mdi-minus
        </v-icon>
        <v-text-field
          dense
          outlined
          class="width-120-px d-inline-block"
        ></v-text-field>
      </template>
    </form-row>

    <form-row>
      <template v-slot:key>
        <label class="custom-form-label">
          {{  $t('facility.receiptFax') }}
        </label>
      </template>
      <template v-slot:value>
        <v-text-field
          dense
          outlined
          class="width-120-px d-inline-block"
        ></v-text-field>
        <v-icon>
          mdi-minus
        </v-icon>
        <v-text-field
          dense
          outlined
          class="width-120-px d-inline-block"
        ></v-text-field>
        <v-icon>
          mdi-minus
        </v-icon>
        <v-text-field
          dense
          outlined
          class="width-120-px d-inline-block"
        ></v-text-field>
      </template>
    </form-row>
    <form-divider />
    <form-row>
      <template v-slot:key>
        <label class="custom-form-label" :for="'ask-cosleeper-age'">
          {{  $t('facility.askCosleeper') }}
        </label>
      </template>
      <template v-slot:value>
        <v-row>
          <v-col cols="12" md="5" lg="3">
            <v-select
              id="ask-cosleeper-age"
              :items="askCosleeperAgeItems"
              dense
              outlined
            ></v-select>
          </v-col>
        </v-row>
      </template>
    </form-row>
    <form-row>
      <template v-slot:key>
        <label class="custom-form-label" :for="'fee-collection'">
          {{  $t('facility.userFeeCollection') }}
        </label>
      </template>
      <template v-slot:value>
        <v-row>
          <v-col cols="12" md="5" lg="3">
            <v-select
              id="fee-collection"
              :items="feeCollectionItems"
              dense
              outlined
            ></v-select>
          </v-col>
          <v-col cols="12" md="7" lg="9">
            {{  $t('facility.noCollectionForChildren') }}
          </v-col>
        </v-row>
      </template>
    </form-row>
    <form-divider />
    <form-row>
      <template v-slot:key>
        <label class="custom-form-label" :for="'floor-plan-pdf-files'">
          {{  $t('facility.facilityFloorPlanPDF') }}
        </label>
      </template>
      <template v-slot:value>
        <v-row>
          <v-col cols="12" md="5" lg="3">
            <v-file-input
              id="floor-plan-pdf-files"
              v-model="pdfPlanFiles"
              color="deep-purple accent-4"
              counter
              :label="$t('facility.uploadFiles')"
              multiple
              prepend-icon="mdi-paperclip"
              outlined
              dense
              hide-details
              :show-size="1000"
            >
              <template v-slot:selection="{ index, text }">
                <v-chip
                  v-if="index < 2"
                  color="deep-purple accent-4"
                  dark
                  small
                >
                  {{ text }}
                </v-chip>

                <span
                  v-else-if="index === 2"
                  class="overline grey--text text--darken-3 mx-2"
                >
              +{{ files.length - 2 }} File(s)
            </span>
              </template>
            </v-file-input>
          </v-col>
        </v-row>
      </template>
    </form-row>
    <form-divider />
    <v-row class="form-row">
      <v-col cols="12" sm="4" md="3" lg="2" class="text-right">
        {{ $t('facility.deleteThisFacility')}}
      </v-col>
      <v-col cols="12" sm="8" md="9" lg="10">
        {{  $t('facility.deleteThisFacilityDescription') }}
        <br>
        <v-btn
          class="ma-2"
          outlined
          color="pink"
        >
          {{  $t('facility.delete') }}
        </v-btn>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import FormRow from '@/components/Form/FormRow'
import FormDivider from '@/components/Form/FormDivider'

export default {
  components: {
    FormRow,
    FormDivider
  },
  props: ['data'],
  data () {
    return {
      managementScreenDisplayOrderItems: [
        {
          text: '1',
          value: 1
        },
        {
          text: '2',
          value: 2
        },
        {
          text: '3',
          value: 3
        }
      ],
      feeCollectionItems: this.$t('facility.data.feeCollectionItems').map((x, i) => ({
        text: x,
        value: i + 1
      })),
      askCosleeperAgeItems: this.$t('facility.data.askCosleeperAgeItems').map((x, i) => ({
        text: x,
        value: i
      })),
      pdfPlanFiles: [],
      rules: {
        applicationName: [
          v => !!v || this.$t('rules.nameIsRequired'),
          v => (v && v.length >= 1) || this.$t('rules.minimumNCharacter', { value: 1 })
        ]
      }
    }
  }
}
</script>

<style scoped>

</style>
