<template>
  <v-main>
    <v-card-actions>
      <v-spacer></v-spacer>
      <v-btn
        color="primary"
        dark
        type="submit"
      >
        <v-icon left>
          mdi-plus
        </v-icon>
        {{  $t('buttons.registerNewParkingLot') }}
      </v-btn>
    </v-card-actions>
    <v-container fluid>
      <v-simple-table class="custom-table">
        <template v-slot:default>
          <thead>
          <tr>
            <th class="text-left">
              {{  $t('facility.title') }}
            </th>
            <th class="text-left">
              {{  $t('facility.carHeight') }}
            </th>
            <th class="text-left width-200-px">
              {{  $t('facility.type') }}
            </th>
            <th class="text-right width-120-px">
              {{  $t('facility.quantity') }}
            </th>
            <th class="text-center width-160-px">
              {{  $t('facility.waitingList') }}
            </th>
            <th class="text-left"></th>
          </tr>
          </thead>
          <tbody>
          <tr
            v-for="(car, i) in data.parkingCars"
            :key="i"
          >
            <td>
              <v-text-field
                v-model="car.title"
                dense
                outlined
                :hide-details="true"
                class="d-inline-block my-3"
              ></v-text-field>
            </td>
            <td>
              <v-text-field
                v-model="car.carHeight[0]"
                dense
                outlined
                type="number"
                min="0"
                :hide-details="true"
                class="width-70-px d-inline-block my-2"
              ></v-text-field>
              ~
              <v-text-field
                v-model="car.carHeight[1]"
                dense
                outlined
                type="number"
                min="0"
                :hide-details="true"
                class="width-70-px d-inline-block my-2"
              ></v-text-field>
            </td>
            <td>
              <v-select
                :items="types"
                v-model="car.type"
                dense
                outlined
                :hide-details="true"
                class="my-3"
              ></v-select>
            </td>
            <td>
              <v-text-field
                v-model="car.quantity"
                dense
                outlined
                type="number"
                min="0"
                :hide-details="true"
                class="my-3"
              ></v-text-field>
            </td>
            <td>
              <v-checkbox
                class="ma-0 float-right"
                v-model="car.isWaiting"
                :hide-details="true"
              ></v-checkbox>
            </td>
            <td>
              <v-btn
                text
                color="error"
                class="float-right my-3"
                @click="deleteCar(i)"
              >
                {{  $t('facility.delete') }}
              </v-btn>
            </td>
          </tr>
          </tbody>
        </template>
      </v-simple-table>
    </v-container>
  </v-main>
</template>

<script>

export default {
  components: {
  },
  props: ['data'],
  data () {
    return {
      types: this.$t('facility.data.carTypes').map((x, i) => ({
        text: x,
        value: i + 1
      }))
    }
  },
  methods: {
    deleteCar (index) {
      this.data.parkingCars.splice(index, 1)
    }
  }
}
</script>

<style scoped lang="scss">

</style>
